import {
    SET_CURRENT_ITEM,
    
} from '../constants/Tools';

const initialState = {
    currentItem: 'hand',
    generateObj: {},
};

export default function tools(state = initialState, action) {
    switch (action.type){
        case SET_CURRENT_ITEM:
            return {...state, currentItem: action.payload};
        case 'INIT_GENERATE_OBJ':
            return {...state, generateObj: action.payload};
        default:
            return {...state};
    }
}